import Vue from 'vue';
import VueTheMask from 'vue-the-mask';
import device from 'vue-device-detector';
import App from './App.vue';
import router from './router';
import store from './store';

Vue.config.productionTip = false;
Vue.use(VueTheMask);
Vue.use(device);

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');
